import { FC, useContext, useEffect, useRef, useState } from 'react'
import { Howl, Howler } from 'howler'
import cx from 'classnames'
import { printDebounce, sleep, waitLongerAfter } from '../../utils/utils'
import { TextBox } from './text-box'
import { MainContext } from '../App'
import './sans.css'

const textStates = [
  ',   ',
  '   ,       ,        ',
  '? ,     !    ',
]

Howler.volume(0.5)

const SansSpeechAudio = new Howl({
  src: 'music/voice_sans.wav',
  preload: true,
})

const SansBgAudio = new Howl({
  src: 'music/sans-bg.mp3',
  preload: true,
  loop: true,
})

export const Sans: FC = () => {
  const { setGlobalStep } = useContext(MainContext)
  const [continueVisible, setContinueVisible] = useState(false)
  const [state, setState] = useState(-1)
  const [text, setText] = useState('')

  useEffect(() => {
    SansBgAudio.play()
  }, [])

  useEffect(() => {
    if (state === -1) return

    const [start, stop] = printDebounce(textStates[state], (char) => {
      if (!waitLongerAfter.includes(char) && char !== ' ')
        SansSpeechAudio.play()
      setText((prevText) => prevText + char)
    })

    start().then((stoppedMannualy) => {
      if (!stoppedMannualy) setContinueVisible(true)
    })

    return () => {
      stop()
      setText('')
    }
  }, [state])

  const handleContinue = () => {
    setContinueVisible(false)
    if (state === 2) {
      SansBgAudio.stop()
      setGlobalStep((p) => p + 1)
    } else {
      setState((p) => p + 1)
    }
  }

  return (
    <div className="sans-wrapper">
      {state === -1 && (
        <button className="sans-button" onClick={() => setState((p) => p + 1)}>
          Lets gooooo
        </button>
      )}
      {state >= 0 && (
        <TextBox
          icon={<img src="img/icon-256.png" alt="sans" width={75} />}
          text={text}
          onClick={handleContinue}
        />
      )}
      <button
        className={cx(
          'sans-button sans-button-continue',
          !continueVisible && 'opacity0',
        )}
        onClick={handleContinue}
      >
        
      </button>
    </div>
  )
}
