import { FC, useContext, useEffect } from 'react'
import { Howl, Howler } from 'howler'
import './genshin-bg.css'
import { MainContext } from '../../modules/App'

Howler.volume(0.5)

const MinecraftBgAudio = new Howl({
  src: 'music/minecraft.mp3',
  preload: true,
  loop: true,
})

export const GenshinBackground: FC = () => {
  const { bgVisible, setBgVisible, bgAudio } = useContext(MainContext)

  useEffect(() => {
    if (!bgVisible || !bgAudio) MinecraftBgAudio.fade(1, 0, 1)
    if (bgVisible && bgAudio) MinecraftBgAudio.fade(0, bgAudio, 1)
  }, [bgAudio, bgVisible])

  useEffect(() => {
    MinecraftBgAudio.play()
    MinecraftBgAudio.fade(0, 1, 2)

    setTimeout(() => {
      setBgVisible(true)
    }, 100)

    return () => {
      MinecraftBgAudio.stop()
    }
  }, [setBgVisible])

  return (
    <div className={`genshin-background ${bgVisible ? '' : 'opacity0'}`}>
      <img className="genshin-video" src="img/panorama_0.webp" alt="" />
      <img className="genshin-video" src="img/panorama_1.webp" alt="" />
      <img className="genshin-video" src="img/panorama_2.webp" alt="" />
      <img className="genshin-video" src="img/panorama_3.webp" alt="" />
      <img className="genshin-video" src="img/panorama_0.webp" alt="" />
    </div>
  )
}
